import * as React from "react";
import { GatsbyImage } from "gatsby-plugin-image";
import { graphql, useStaticQuery } from "gatsby";
import * as Styles from "./bio.module.scss";

const Bio = () => {
  const data = useStaticQuery(graphql`
    query imageArray {
      allFile(filter: { relativePath: { regex: "/profile/i" } }) {
        nodes {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
    }
  `);

  const totalImages = 10; // This has to match the iterations in sidebar.module.scss

  let imageNodes = data.allFile.nodes;

  while (imageNodes.length < totalImages) {
    imageNodes.push(...imageNodes);
  }

  imageNodes = imageNodes.slice(0, 9); // check that this actually works

  return (
    <div className={Styles.bio}>
      <div className={Styles.text}>
        Hi, my name is <span className={Styles.strong}>Ashwin</span>.
      </div>
      <div className={Styles.profileImages}>
        {imageNodes.map((node, i: number) => {
          const key = `image-${i}`;
          return (
            <div key={key} className={Styles.imageContainer}>
              <GatsbyImage
                alt=""
                image={node.childImageSharp.gatsbyImageData}
                className={`${Styles.image} ${Styles[`image${i}`]}`}
              />
            </div>
          );
        })}
      </div>
      <div className={Styles.text}>
        For those of you new here, it's pronounced{" "}
        <span className={Styles.strong}>Osh-Win</span>. I am a{" "}
        <span className={Styles.strong}>Software Engineer III</span> at{" "}
        <span className={Styles.strong}>DEPTÂ®</span>. Previously, I was a
        software engineer at <span className={Styles.strong}>Medtronic</span>. I
        have a bachelor's degree in neuroscience from UCLA, and a master's
        degree in biomedical engineering from Arizona State University. My
        education and career trajectory has been unique and bizarre, ask me
        about it.
        <p>Other fun facts: </p>
        <ul>
          <li>
            I was a barista and supervisor at Starbucks for 2 years during grad
            school
          </li>
          <li>I speak Tamil marginally fluently</li>
          <li>I was obsessed with Speech and Debate in high school</li>
          <li>I love to write all sorts of things</li>
          <li>I play and record music</li>
          <li>
            I have a love for darkroom photography (and built my own darkroom)
          </li>
          <li>I love gardening</li>
          <li>
            I once drove a 1993 4Runner with 330,000 miles across the
            Southwestern US
          </li>
          <li>I'm a vegetarian</li>
          <li>
            I'm a software engineer because I love math, first and foremost
          </li>
          <li>
            I've been to 30 US states, 8 countries (including India 10+ times),
            but never been south of the equator
          </li>
        </ul>
        <p>I live in Colorado.</p>
      </div>
    </div>
  );
};

export default Bio;
